import resolve from "resolve"
import path from "path"
import { pathExists } from "fs-extra"

import { Plugin } from "../plugin"
import { ServerContext } from "../server/index"
import { DEFAULT_EXTERSIONS } from "../constants"
import { cleanUrl } from "../utils"

export function resolvePlugin(): Plugin {
  let serverContext: ServerContext
  return {
    name: "vite:resolve",
    configureServer(s) {
      // ‰øùÂ≠òÊúçÂä°Á´Ø‰∏ä‰∏ãÊñá
      serverContext = s
    },
    async resolveId(id: string, importer?: string) {
      // 1. ÁªùÂØπË∑ØÂæÑ
      if (path.isAbsolute(id)) {
        if (await pathExists(id)) {
          return { id }
        }
        // Âä†‰∏ä root Ë∑ØÂæÑÂâçÁºÄÔºåÂ§ÑÁêÜ /src/main.tsx ÁöÑÊÉÖÂÜµ
        id = path.join(serverContext.root, id)
        if (await pathExists(id)) {
          return { id }
        }
      }
      // 2. Áõ∏ÂØπË∑ØÂæÑ
      else if (id.startsWith(".")) {
        if (!importer) {
          throw new Error("`importer` should not be undefined")
        }
        const hasExtension = path.extname(id).length > 1
        let resolvedId: string
        // 2.1 ÂåÖÂê´Êñá‰ª∂ÂêçÂêéÁºÄ
        // Â¶Ç ./App.tsx
        if (hasExtension) {
          resolvedId = resolve.sync(id, { basedir: path.dirname(importer) })
          if (await pathExists(resolvedId)) {
            // return { id: resolvedId }
            return { id }
          }
        } 
        // 2.2 ‰∏çÂåÖÂê´Êñá‰ª∂ÂêçÂêéÁºÄ
        // Â¶Ç ./App
        else {
          // ./App -> ./App.tsx
          for (const extname of DEFAULT_EXTERSIONS) {
            try {
              const withExtension = `${id}${extname}`
              resolvedId = resolve.sync(withExtension, {
                basedir: path.dirname(importer),
              })
              if (await pathExists(resolvedId)) {
                // return { id: resolvedId }
                return { id: withExtension }
              }
            } catch (e) {
              continue
            }
          }
        }
      }
      return null
    }
  }
}
