import { Loader, Plugin } from "esbuild"
import { BARE_IMPORT_RE } from "../constants"

// Áî®Êù•ÂàÜÊûê es Ê®°Âùó import/export ËØ≠Âè•ÁöÑÂ∫ì
import { init, parse } from "es-module-lexer"
import path from "path"
// ‰∏Ä‰∏™ÂÆûÁé∞‰∫Ü node Ë∑ØÂæÑËß£ÊûêÁÆóÊ≥ïÁöÑÂ∫ì
import resolve from "resolve"
// ‰∏Ä‰∏™Êõ¥Âä†Â•ΩÁî®ÁöÑÊñá‰ª∂Êìç‰ΩúÂ∫ì
import fs from "fs-extra"
// Áî®Êù•ÂºÄÂèëÊâìÂç∞ debug Êó•ÂøóÁöÑÂ∫ì
import createDebug from "debug"

import{ normalizePath } from '../utils'

const debug = createDebug("dev")

export function preBundlePlugin(deps: Set<string>): Plugin {
  return {
    name: "esbuild:pre-bundle",
    setup(build) {
      build.onResolve(
        {
          filter: BARE_IMPORT_RE,
        },
        (resolveInfo) => {
          const { path: id, importer } = resolveInfo
          const isEntry = !importer

          // ÂëΩ‰∏≠ÈúÄË¶ÅÈ¢ÑÁºñËØëÁöÑ‰æùËµñ
          if (deps.has(id)) {
            // Ëã•‰∏∫ÂÖ•Âè£ÔºåÂàôÊ†áËÆ∞ dep ÁöÑ 
            
            return isEntry
              ? {
                  path: id,
                  namespace: "dep",
                }
              : {
                  // Âõ†‰∏∫Ëµ∞Âà∞ onResolve ‰∫ÜÔºåÊâÄ‰ª•ËøôÈáåÁöÑ path Â∞±ÊòØÁªùÂØπË∑ØÂæÑ‰∫Ü
                  path: resolve.sync(id, { basedir: process.cwd() }),
                }
          }
        }
      )

      // ÊãøÂà∞Ê†áËÆ∞ÂêéÁöÑ‰æùËµñÔºåÊûÑÈÄ†‰ª£ÁêÜÊ®°ÂùóÔºå‰∫§Áªô esbuild ÊâìÂåÖ
      build.onLoad(
        {
          filter: /.*/,
          namespace: "dep",
        },
        async (loadInfo) => {
          await init
          
          const id = loadInfo.path
          const root = process.cwd()

          const entryPath = resolve.sync(id, { basedir: root })

          const code = await fs.readFile(entryPath, "utf-8")
          const [imports, exports] = await parse(code)

          let relativePath = normalizePath(path.relative(root, entryPath))
          if (
            !relativePath.startsWith('./') &&
            !relativePath.startsWith('../') &&
            relativePath !== '.'
          ) {
            relativePath = `./${relativePath}`
          }

          let proxyModule = []

          // cjs
          if (!imports.length && !exports.length) {
            // ÊûÑÈÄ†‰ª£ÁêÜÊ®°Âùó
            const res = require(entryPath)
            const specifiers = Object.keys(res)
            proxyModule.push(
              `export { ${specifiers.join(",")} } from "${relativePath}"`,
              `export default require("${relativePath}")`
            )
          } else {
            // esm Ê†ºÂºèÊØîËæÉÂ•ΩÂ§ÑÁêÜÔºåexport * ÊàñËÄÖ export default Âç≥ÂèØ
            if (exports.includes("default")) {
              proxyModule.push(`import d from "${relativePath}"export default d`)
            }
            proxyModule.push(`export * from "${relativePath}"`)
          }
          debug("‰ª£ÁêÜÊ®°ÂùóÂÜÖÂÆπ: %o", proxyModule.join("\n"))
          const loader = path.extname(entryPath).slice(1)

          return {
            loader: loader as Loader,
            contents: proxyModule.join("\n"),
            resolveDir: root,
          }
        }
      )
    }
  }
}